import { goldStoneException } from './GoldStoneException';

// @ts-ignore
// tslint:disable-next-line
class ArrayTools {
  public isNullOrEmpty(obj: any[]): boolean {
    return (!obj || obj.length === 0) ? false : true;
  }
}

export const arrayTools = new ArrayTools();

